.nav-mobile-1 {
    height: 95px;

    .nav-mobile-2 {
        position: fixed;
        width: 100%;

        .nav-mobile-3 {
            margin: 10px;
            height: 100%;
            border-radius: 10px;
            background-color: #181818;
            display: flex;
            flex-direction: column;
            padding-bottom: 7px;

            .nav-mobile-3-1 {
                display: flex;
                padding-right: 10px;

                .logo-mobile {
                    height: 50px;
                    margin: 10px;
                }

                #button-mobile-1 {
                    border-radius: 10px;
                    background-color: #282828;
                    border: 0px;
                    margin: auto 0 auto auto;
                    padding: 9px 14.23px;
                    color: #FFF;
                    font-size: x-large;
                }

                #button-mobile-2 {
                    border-radius: 10px;
                    background-color: #282828;
                    border: 0px;
                    margin: auto 0 auto auto;
                    padding: 9px 14.23px;
                    color: #FFF;
                    font-size: x-large;
                    display: none;
                }
            }

            .nav-mobile-3-2 {
                display: none;
                flex-direction: column;

                .link-mobile {
                    margin: 5px 10px;
                    color: #FFF;
                    text-decoration: none;
                    padding: 10px;
                    background-color: #282828;
                    border-radius: 10px;
                    font-weight: bolder;
                }

                .link-mobile:hover {
                    background-color: #404040;
                }
            }
        }
    }
}