body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: medium;
  background-color: #121212;
  color: #FFF;
}

::-webkit-scrollbar {
  width: 7px;
}

::-webkit-scrollbar-track {
  background: #121212;
}

::-webkit-scrollbar-thumb {
  background: #282828;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #404040;
}