.home-link {
    text-decoration: none;
    color: #FFF;
}

.home-3 {
    text-align: center;
    display: block;
    font-size: xx-large;
    background-color: #282828;
}

.home-1 {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    .home-2 {
        width: 280px;
        height: 250px;
        margin: 10px;
        background-color: #282828;
        border-radius: 10px;
        padding: 10px;

        .home-img {
            width: 100%;
            height: 180px;
            border-radius: 10px;
            border: 0;
            object-fit: cover;

        }

        .home-title {
            word-wrap: break-word;
            margin: 3px 0 0 0;
        }
    }

    .home-2:hover {
        background-color: #404040;
    }
}