.nav-desktop-1 {
    height: 95px;

    .nav-desktop-2 {
        position: fixed;
        width: 100%;

        .nav-desktop-3 {
            margin: 10px;
            height: 70px;
            border-radius: 10px;
            background-color: #181818;
            display: flex;
            padding-right: 10px;

            .logo-desktop {
                height: 50px;
                margin: 10px;
            }

            .link-desktop {
                margin: auto 10px auto 0;
                color: #FFF;
                text-decoration: none;
                padding: 10px;
                background-color: #282828;
                border-radius: 10px;
                font-weight: bolder;
            }

            .link-desktop:hover {
                background-color: #404040;
            }
        }
    }
}