.pagenotfound {
    width: 200px;
    text-align: center;
    margin: 0 auto;

    .pagenotfound-h1 {
        margin: 0;
        font-size: 100px;
    }

    .pagenotfound-span {
        margin: 0 0 20px 0;
        font-size: 25px;
        display: block;
    }

    .pagenotfound-link {
        padding: 20px 0;
        background-color: #282828;
        border-radius: 10px;
        display: block;
        text-decoration: none;
        color: #FFF;
    }

    .pagenotfound-link:hover {
        background-color: #404040;
        cursor: pointer;
    }
}