.blogs-link {
    text-decoration: none;
    color: #FFF;
}

.blogs-1 {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    .blogs-2 {
        width: 280px;
        height: 250px;
        margin: 10px;
        background-color: #282828;
        border-radius: 10px;
        padding: 10px;

        .blogs-img {
            width: 100%;
            height: 180px;
            border-radius: 10px;
            border: 0;
            object-fit: cover;
        }

        .blogs-title {
            word-wrap: break-word;
            margin: 3px 0 0 0;
        }
    }

    .blogs-2:hover {
        background-color: #404040;
    }
}